import React, { Component, ReactElement } from 'react';
import { Card, Button } from 'react-bootstrap';
import key from '../../../core/key/react-elements.key';
import { NewsModel } from '../../../core/models/news.model';
import likeImg from '../../../styles/img/like.png';
import dislikeImg from '../../../styles/img/dislike.png';
import './card-news.css';

class CardNewsComponent extends Component<any> {
  
  private renderNews(): Array<ReactElement> | void {
    const { news, role, onEditNews, onLike, onDislike } = this.props;

    if (news) {
      news.reverse();
      return news.map((element: NewsModel) => (
        <Card className="text-left card-shadow mb-5" key={ key() }>
          <Card.Img 
            className="card-img"
            variant="top" 
            src={ element.img }
            alt={ element.title } 
          />

          <Card.Body>
            <Card.Title>{ element.title }</Card.Title>
            <Card.Text className="card-text">
              { element.information }
            </Card.Text>
          </Card.Body>

          <Card.Footer className="text-right">
            {
              role === 1 ?
                <Button 
                  className="mr-2" 
                  variant="outline-info"
                  onClick={ () => onEditNews(element) }
                >
                  Editar
                </Button>
              :
                <>
                  <Button 
                    className="mr-2" 
                    variant="outline-dark"
                    onClick={ () => onLike(element) }
                  >
                    <img src={ likeImg } />
                    <label>{ element.like }</label>
                  </Button>

                  <Button 
                    variant="outline-dark"
                    onClick={ () => onDislike(element) }
                  >
                    <img src={ dislikeImg } />
                    <label>{ element.dislike }</label>
                  </Button>
                </>  
            }          
          </Card.Footer>
        </Card>
      ));
    }

  }

  render() {
    return (
      <>
        { 
          !this.props.news ?   
            <>
              <h1>Cargando...</h1>  
            </> 
          :
            this.renderNews()
        }
      </>
    );
  }
}

export default CardNewsComponent;